import React,{useState} from 'react';
const AddComment=({article,setArticleInfo})=>{
    const [userName,setUserName]=useState('');
    const [comment,setComment]=useState('');
    const commentSubmit=async(event)=>{
        event.preventDefault();
        const result= await fetch(`/api/articles/${article}/comment`,{
            method:'POST',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({username:userName,comment:comment}),
        });
        const body=await result.json();
        await setArticleInfo(body);  
    }
    return(
        <div>
            <form action="" method="post" onSubmit={(event)=>{commentSubmit(event)}}>
                <label htmlFor="username">Your name: </label>
                <input type="text" name="username" value={userName}  onChange={(e)=>{setUserName(e.target.value)}} placeholder="your name"/><br/>
                <label htmlFor="comment">
                    Your Comment
                    <textarea name="comment" valuel={comment} id="" cols="30" rows="10" onChange={(e)=>{setComment(e.target.value)}}></textarea>
                </label>
                <button type='submit'> Comment</button>
            </form>
        </div>
    );
}


export default AddComment;
