import React,{useState,useEffect} from 'react';
import articleContent from './article-content';
import ArticleList from '../components/ArticleList';
import CommentList from '../components/commentList';
import UpvoteArticle from '../components/UpvoteArticle';
import AddComment from '../components/AddComment';

const ArticlePage=({match})=>{

const [articleInfo,setArticleInfo]=useState({upvotes:0,comments:[],upvoteUpdate:0});

const name=match.params.name;

const article=articleContent.find(article=>article.name===name);

useEffect(()=>{
    const fetchData =async()=>{
        const result = await fetch(`/api/articles/${name}`);
        const body= await result.json();
        console.log(body);
        setArticleInfo(body);
    }
    fetchData();
},[name],articleInfo.upvoteUpdate)
if(!article){
    return(
        <h1>'Article not found!'</h1>
    )
}
    return(
        <React.Fragment>
            <h1>{article.title}</h1>
    <p>This article has been upvoted {articleInfo.upvotes} times <UpvoteArticle article={name} setArticleInfo={setArticleInfo}></UpvoteArticle></p>
            { article.content.map((paragraphe,key)=>{
                return(
                    <p key={key}>{paragraphe}</p>
                )
                }
            )}
    <h2>comments</h2>
    <AddComment article={name} setArticleInfo={setArticleInfo}></AddComment>
    <CommentList comments={articleInfo.comments}></CommentList>
            <div>
            <h1>Related articles</h1>
                <ArticleList articles={articleContent.filter(item=>item.name!==name)}></ArticleList>
            </div>
        </React.Fragment>
    )
}

export default ArticlePage;