import React from 'react';

const UpvoteArticle=({article,setArticleInfo,upVotes})=>{
    const upvoteArticle=async(article)=>{
        const result = await fetch(`/api/articles/${article.article}/upvote`,{
            method:'POST',
        })

        const body=await result.json();

        await setArticleInfo(body);
        }

    return(
        <>
        <button className="btn btn-success" type='button' onClick={()=>{upvoteArticle({article})}}>Upvote Article</button>
        </>
    )
}

export default UpvoteArticle;